@import url(https://fonts.googleapis.com/css?family=B612+Mono|Lato&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html{
  scroll-behavior:smooth;
}

.App {
  text-align: center;
  background-color: #ffffff;
  font-family: 'Lato', sans-serif;
}

.container{
  display: flex;
  padding: 10px 15px;
  margin: 3em auto;
}

.col-1{
  background-image: url(/v9-geckos-team-04/static/media/speech_to_text.04f03d28.svg);
  flex:1 1;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: contain;
}

.col-2{
  flex:1 1;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.subTitle{
  font-weight: 400;
  font-size: 2em;
  color:#6c61f6;
}

.textarea{
  overflow: auto;
  width: 80%;
  height: 300px;
  margin: 20px auto;
  border: none;
  border-radius: 10px;
  box-shadow: 0 0 3px 5px rgba(0,0,0,0.12);
  padding: 15px;
  font-size: 14px;
}

.lang-select {
  margin: 1em auto;
  color: #39364d;
  font-size: 18px;
}

.lang-select select {
  margin: 10px;
  padding: 10px;
  background-color: #fff;
  background-image: url('data:image/svg+xml;charset=US-ASCII,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20width%3D%22292.4%22%20height%3D%22292.4%22%3E%3Cpath%20fill%3D%22%23007CB2%22%20d%3D%22M287%2069.4a17.6%2017.6%200%200%200-13-5.4H18.4c-5%200-9.3%201.8-12.9%205.4A17.6%2017.6%200%200%200%200%2082.2c0%205%201.8%209.3%205.4%2012.9l128%20127.9c3.6%203.6%207.8%205.4%2012.8%205.4s9.2-1.8%2012.8-5.4L287%2095c3.5-3.5%205.4-7.8%205.4-12.8%200-5-1.9-9.2-5.5-12.8z%22%2F%3E%3C%2Fsvg%3E');
  font-weight: bold;
  font-size: 14px;
  border-radius: 5px;
  color: #39364d;
}

.pulse {
  -webkit-transition: all 200ms ease;
  transition: all 200ms ease;
  -webkit-animation: pulse 2s infinite ease-in-out;
          animation: pulse 2s infinite ease-in-out;
}

@-webkit-keyframes pulse {
  0%    {opacity: 1;}
  50%   {opacity: .5;}
  100%  {opacity: 1;}
}

@keyframes pulse {
  0%    {opacity: 1;}
  50%   {opacity: .5;}
  100%  {opacity: 1;}
}

@media (max-width: 480px) {

  .col-1{
    display: none;
  }

  .container {
    flex-direction: column;
    margin: 0;
  }

  .title{
    font-size: 45px !important;

  }
  .navbar {
    position: static;
  }
  .textarea{
    height: 246px;
  }
  .feature-container{
    flex-direction: column;
  }

  .about-para{
    padding: 20px !important;
    text-align: left;
  }
}

.header{
  display: flex;
  justify-content: center;
  align-items: center;
}
.title {
    color: #453adf;
    font-size: 65px;
    font-family: 'B612 Mono', monospace;
    margin:8px 0;
    flex: 1 1;
  }

  .navbar {
    margin: 1em;
    position: absolute;
    top: 0;
    right: 0;

  }

  .navbar ul {
    padding: 0;
  }

  .navbar li{
    list-style-type:none;
    display: inline-block;
    margin: 0 6px;
    padding-bottom: 3px;
    /* border-bottom: 2px solid transparent;
    transition: all 300ms ease; */
    border-bottom: 2px solid #FF4242;
  }

  .navbar li a{
    text-decoration: none;
    color:#FF4242;
    font-size: 18px;
  }

  .navbar li:hover {
    border-bottom: 2px solid #FF4242;
  }


  @media (max-width: 480px) {

    .navbar {
      position: static;

    }
    .subTitle {
      font-size: 1.5em;
      margin: 0;
    }
  }

.footer{
    padding:8px;
    color: #ffffff;
    background-color: #4c4c56;
    font-weight: 300;


  }

  .creators{
    color:#ffffff;
  }
  .profiles {
    display: flex;
    justify-content: space-between;
  }
.profiles a{
    color:#ffffff;
    text-decoration: none;
}
  .footer p{
      margin:5px;
  }
  .icon{
      margin:3px;
  }

.btn {
  padding: 10px;
  background-color: #39364d;
  color: #ffffff;
  font-size: 14px;
  border-radius: 5px;
  font-weight: bold;
  margin: 0 5px 20px 5px;
  -webkit-transition: all 100ms ease;
  transition: all 100ms ease;
}

.btn:hover, .btn:focus {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.btn:active {
  background-color: #ffffff;
  color: #2c274b
}

.start, .stop {
  padding: 10px;
  background-color: #6c61f6;
  color: #ffffff;
  font-weight: bold;
  font-size: 14px;
  border-radius: 5px;
  -webkit-transition: all 100ms ease-out;
  transition: all 100ms ease-out;
}

.start:hover, .stop:hover, .start:focus, .stop:focus {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

h1{
    color:#3f3c56;
    font-weight: lighter;
    font-size: 24px;
}

.about-para{
    font-size: 18px;
    font-family: Helvetica;
    padding: 30px 100px;
    line-height: 20px;
}

.features{
    background-color: #f2f2f2;
    padding: 40px;
}
.feature-container{
    display: flex;
    padding: 20px;
}

.feature-card{
    flex:1 1;
    margin: 0 26px;
}

img{
    height:50px;
    width:50px;
}
h2{
    color:#453adf;
}
p{
    /* font-size: 1.2em; */
    line-height: auto;
}

